@import "../../../scss/color";
@import "../../../scss/mixins";
@import '../../../scss/utils';

.default {
  font-weight: 400;
  font-size: 25px;
  color: $white;
  text-align: center;
  padding: 8px 10px;
  @media (max-width:608px) {
    font-size: 20px;
  }
}

.card-light-green {
  background-color: $white_green;
  h3 {
    margin: 0;
  }
  p {
    margin: 0;
  }
  // border-radius: 10px;
  // width: 49%;
  // height: fit-content;
  @media (max-width:894px) {
    width: 100%;
    margin-bottom: 10px;
  }
  @include general;
}
